/**
 * Datalist
 * Populates a datalist for an input
 */

var React = require('react')
var Types = React.PropTypes

var Search = React.createClass({displayName: 'Search',

  propTypes: {
    id    : Types.string.isRequired,
    items : Types.array
  },

  getDefaultProps:function() {
    return {
      items: []
    }
  },

  getOption:function(option, i) {
    return React.createElement("option", {key:  "option_" + i, value: option })
  },

  render:function() {
    return (
      React.createElement("datalist", {id:  this.props.id}, 
         this.props.items.map(this.getOption)
      )
    )
  }

})

module.exports = Search



