/**
 * Search
 */

var DataList = require('./datalist')
var React    = require('react')
var Types    = React.PropTypes
var _id      = 0

var Search = React.createClass({displayName: 'Search',

  propTypes: {
    datalist  : Types.array,
    onChange  : Types.func.isRequired,
    threshold : Types.number
  },

  getDefaultProps:function() {
    return {
      threshold: 2
    }
  },

  getInitialState:function() {
    return {
      id: _id++
    }
  },

  render:function() {
    var inputId = "__search_" + this.state.id
    var listId  = "__list_" + this.state.id

    return (
      React.createElement("div", {className: "ars-search"}, 
        React.createElement("label", {className: "ars-search-label", htmlFor: inputId }, "Search"), 

        React.createElement("input", {ref: "input", type: "search", className: "ars-search-input", onChange:  this._onChange, placeholder: "Search", list: listId }), 

        React.createElement(DataList, {id: listId, items:  this.props.datalist})
      )
    )
  },

  _onChange:function(e) {
    var query = this.refs.input.getDOMNode().value

    this.props.onChange(query.length >= this.props.threshold ? query : '')
  }

})

module.exports = Search



